/* COIN */
.coin {
  position: relative;
  width: 300px;
  height: 300px;
  margin: 50px auto;
  transform-style: preserve-3d;
  -webkit-animation: rotate3d 3s linear infinite;
          animation: rotate3d 3s linear infinite;
  transition: all 0.3s;
}

.coin__front,
.coin__back {
  position: absolute;
  width: 300px;
  height: 300px;
  border-radius: 50%;
  overflow: hidden;
  background-color: #E8D0BB;
}
.coin__front:after,
.coin__back:after {
  content: "";
  position: absolute;
  left: -150px;
  bottom: 100%;
  display: block;
  height: 200px;
  width: 600px;
  background: #fff;
  opacity: 0.3;
  -webkit-animation: shine linear 4s infinite;
          animation: shine linear 4s infinite;
}

.coin__front {
  background-image: url("https://www.hbarcoinflip.com/coin_head1.png");
  background-size: cover;
  transform: translateZ(2px);
}

.coin__back {
  background-image: url("https://www.hbarcoinflip.com/coin_tail1.png");
  background-size: cover;
  transform: translateZ(-2px) rotateY(180deg);
}

